import s from './MessagesList.module.css'
import Message from "./Message/Message";
import React from "react";
import {Field, reduxForm} from "redux-form";
import {InputCreator} from "../../common/FormsControls/FormsControls";
import {maxLengthCreator, requiredField} from "../../../utils/validators/validators";



let maxLength = maxLengthCreator(100);

const Textarea = InputCreator('textarea');

const AddMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field component={Textarea} validate={[requiredField, maxLength]}  name={'messageTextareaValue'}
                   placeholder='add message'>
            </Field>


            <button>Add</button>
        </form>
    );
};

const AddMessageReduxForm = reduxForm({form: 'dialogAddMessageForm'})(AddMessageForm);

const MessagesList = (props) => {

    let messages = props.messagesData.map((m) => {
        return <Message key={m.id} text={m.message}/>
    });


    const addMessage = (values) => {
        props.addMessage(values.messageTextareaValue)
    }


    return (
        <div className={s.wrapperBox}>
            <div className={s.messagesList}>
                {messages}
            </div>


            <div className={s.wrapper}>
                <AddMessageReduxForm onSubmit={addMessage}/>
            </div>
        </div>
    );
};

export default MessagesList;