import DialogList from "./DialogList/DialogList";
import s from './Messages.module.css'

import {Redirect} from "react-router-dom"
import MessagesList from "./MesssagesList/MessagesList";
import React from "react";

const Messages = (props) => {
    if (!props.auth) return(

        <Redirect to='/login'/>
    )
    else {
        return (
            <div className={`${s.wrapper} content`}>
                <DialogList dialogData={props.dialogData}/>


                <MessagesList addMessage={props.addMessage}
                              updateMessageText={props.updateMessageText}
                              dialogData={props.dialogData}
                              messageTextareaValue={props.messageTextareaValue}
                              messagesData={props.messagesData}/>

            </div>
        );

    }


};
export default Messages;